@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;500;600&family=Lato:wght@100;300;400;700&family=Montserrat:wght@100;300;400;500;700;800&family=Open+Sans:wght@300;400;600;700&family=Roboto:wght@700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{overflow:hidden;box-sizing:border-box;padding:0;margin:0;max-width:1200px;margin:auto;background-image:url(/learn-japanese/static/media/bg.bf7773eb.jpg);background-position:right top;background-repeat:no-repeat;background-size:70%;height:100%;display:flex;justify-content:center}@media (max-width: 600px){.App{background-size:100%}}.container{max-width:900px}@media (max-width: 800px){.container{width:700px}}@media (max-width: 600px){.container{width:340px}}

.header{font-size:13px;font-weight:bold;box-sizing:border-box;font-family:'Lato', sans-serif;width:100%;height:100px;display:flex;justify-content:space-between;align-items:center}.header .list-item{display:flex;align-items:center;width:50%;justify-content:space-around}@media (max-width: 600px){.header .list-item{display:none}}.header .logo img{width:100px}@media (max-width: 400px){.menu{width:30px;float:right;display:block}}

.btn{font-family:'Lato', sans-serif;padding:1em;border-radius:10px;cursor:pointer;color:#FBF1C3;background-color:#372E7F;border:none;justify-content:center;align-items:center;display:flex;font-weight:700;font-size:13px}@media (max-width: 600px){.btn{font-size:20px}}.btn-transparant{border:solid black 1px;background:none;color:black}

.main{height:490px;width:25%}@media (max-width: 800px){.main{width:40%;height:400px}}@media (max-width: 600px){.main{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;height:750px}}.main h1{font-family:'Roboto', sans-serif;font-weight:900;font-size:3em;letter-spacing:.07em}@media (max-width: 600px){.main h1{font-size:35px}}.main p{font-family:'Lato', sans-serif;font-size:15px;line-height:1.5em}@media (max-width: 600px){.main p{font-size:20px}}.main p span{display:block}.main .btn{margin-top:3em}

ul{list-style:none;display:flex;padding:0}@media (max-width: 600px){ul{display:block;margin:0;padding:0}}ul .block{width:20%;padding-right:7em}@media (max-width: 800px){ul .block{width:30%;padding-right:30px}}@media (max-width: 600px){ul .block{box-sizing:border-box;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:20px}}ul .block .icon{width:50px}ul .block p{font-size:15px;font-family:'Lato', sans-serif;line-height:1.5em;margin-top:1.5em}ul .block p .bold{display:inline-block;font-weight:bold}

